<template>
  <div id="app">
    <vs-row vs-justify="center" class="watchs">
      <vs-col
        v-for="index in (0, 6)"
        :key="index"
        vs-type="flex"
        vs-justify="center"
        vs-align="flex-start"
        vs-w="4.7"
      >
        <StopWatch
          :timer="timers[index]"
          :index="index"
          @saveLocalStorage="saveLocalStorage"
        />
      </vs-col>
      <vs-col
        vs-type="flex"
        vs-justify="center"
        vs-align="flex-start"
        vs-w="4.7"
      >
        <vs-button
          :color="isSomeRunning ? 'warning' : 'primary'"
          class=""
          @click="toggleAllStatus"
          >All {{ isSomeRunning ? "Stop" : "Start" }}</vs-button
        >
      </vs-col>
      <vs-col
        vs-type="flex"
        vs-justify="center"
        vs-align="flex-start"
        vs-w="4.7"
      >
        <vs-button color="danger" class="" @click="clearAll"
          >All Clear</vs-button
        >
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
import StopWatch from "./components/StopWatch.vue";

class Timer {
  constructor(diffTime = 0, startTime = 0, isRunning = false) {
    this.nowTime = 0;
    this.diffTime = diffTime;
    this.startTime = startTime;
    this.animateFrame = 0;
    if (isRunning) {
      //LocalStorageから呼ばれた時に、9時間回ってるならクリアしちゃおう的な
      if ((Date.now() - this.startTime) / 1000 / 60 / 60 >= 9) {
        this.clear();
        return;
      }
      this.restart();
    } else {
      this.isRunning = false;
    }
  }
  start() {
    var vm = this;
    vm.startTime = Math.floor(Date.now() - vm.diffTime || 0);
    (function loop() {
      vm.nowTime = Math.floor(Date.now());
      vm.diffTime = vm.nowTime - vm.startTime;
      vm.animateFrame = requestAnimationFrame(loop);
    })();
    vm.isRunning = true;
  }
  restart() {
    var vm = this;
    (function loop() {
      vm.nowTime = Math.floor(Date.now());
      vm.diffTime = vm.nowTime - vm.startTime;
      vm.animateFrame = requestAnimationFrame(loop);
    })();
    vm.isRunning = true;
  }
  stop() {
    this.isRunning = false;
    cancelAnimationFrame(this.animateFrame);
  }
  clear() {
    this.stop();
    this.startTime = 0;
    this.diffTime = 0;
    this.animateFrame = 0;
    this.isRunning = false;
  }
  get hours() {
    return Math.floor(this.diffTime / 1000 / 60 / 60);
  }
  get minutes() {
    return Math.floor(this.diffTime / 1000 / 60) % 60;
  }
  get seconds() {
    return Math.floor(this.diffTime / 1000) % 60;
  }
  get milliSeconds() {
    return Math.floor((this.diffTime % 1000) / 10);
  }
}

export default {
  name: "app",
  components: {
    StopWatch
  },
  data() {
    return {
      timers: []
    };
  },
  methods: {
    saveLocalStorage: function() {
      let obj = this.timers.reduce((o, c, i) => {
        return c
          ? Object.assign(
              o,
              {
                [i]: {
                  diffTime: c.diffTime,
                  isRunning: c.isRunning,
                  startTime: c.startTime
                }
              },
              {}
            )
          : o;
      }, {});
      localStorage.setItem("timers", JSON.stringify(obj));
    },
    clearAll: function() {
      this.timers.forEach(timer => {
        timer.clear();
      });
      this.saveLocalStorage();
    },
    toggleAllStatus: function() {
      let isSomeRunning = this.isSomeRunning;
      this.timers.forEach((timer, index) => {
        if (index === 0) return;
        if (isSomeRunning) {
          timer.stop();
        } else {
          timer.start();
        }
      });
      this.saveLocalStorage();
    }
  },
  computed: {
    isSomeRunning: function() {
      return this.timers.some(timer => {
        return timer.isRunning;
      });
    }
  },
  head() {
    return {
      title: this.title,
      meta: [
        {
          hid: "description",
          name: "description",
          content: "6個あるストップウォッチ的ななにか"
        }
      ]
    };
  },
  created() {
    let item = localStorage.getItem("timers");
    if (item) {
      let obj = JSON.parse(localStorage.getItem("timers"));
      [...Array(7)].forEach((_, i) => {
        this.timers.splice(
          i,
          0,
          new Timer(obj[i].diffTime, obj[i].startTime, obj[i].isRunning)
        );
      });
    } else {
      [...Array(7)].forEach((_, i) => {
        this.timers.splice(i, 0, new Timer());
      });
    }
  }
};
</script>

<style lang="stylus">
body
  background-color black
  background-image url("./assets/brushed-alum.png")

#app
  font-family 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
  text-align center
  color #2c3e50
  margin-top 10px

.vs-col button
  margin 1vw
  height auto
  width 100%
  font-size 3rem
</style>
