<template>
  <div class="adjust-box box-1x1" @click="clickTimer">
    <vs-card class="cardx" :style="statusBgcolor">
      <div slot="header">
        <h2 :style="{ color: colors[index] }">{{ index | repIndex }}</h2>
        <h3></h3>
      </div>
      <div slot="media">
        <h1>
          {{ timer.hours }}-{{ timer.minutes | zeroPad }}-{{
            timer.seconds | zeroPad
          }}-{{ timer.milliSeconds | zeroPad }}
        </h1>
      </div>
    </vs-card>
  </div>
</template>

<script>
export default {
  name: "StopWatch",
  props: ["timer", "index"],
  data() {
    return {
      colors: [
        "white",
        "black",
        "#EE7700",
        "green",
        "#e50011",
        "#0064B3",
        "#5F1885"
      ]
    };
  },
  methods: {
    clickTimer: function() {
      if (this.timer.isRunning) {
        this.timer.stop();
      } else {
        this.timer.start();
      }
      this.$emit("saveLocalStorage");
    }
  },
  computed: {
    indexColorStyle: function(index) {
      // let
      return { colors: this.colors[index] };
    },
    statusBgcolor: function() {
      if (this.timer.isRunning)
        return {
          background: "linear-gradient(#FFFFFF,#a8c9ffDD, #1f74ffDD ) "
        };
      else if (this.timer.diffTime > 1)
        return { background: "linear-gradient(#FFFFFF,#FFF2B8DD,#FCC800DD)" };
      else return { background: "#d3d3d3e6" };
    }
  },
  filters: {
    zeroPad: function(value, num) {
      num = 2;
      return value.toString().padStart(num, "0");
    },
    //時計がZ順で配置されるので、インデックスを調整
    repIndex: function(index) {
      return [0, 1, 4, 2, 5, 3, 6][index];
    }
  }
};
</script>

<style lang="stylus">
.adjust-box
  margin 5px
  position relative
  width 100%
  height auto

.box-1x1:before
  content ""
  display block
  padding-top 100%

div.cardx
  position absolute
  top 0
  left  0
  width 100%
  height 100%
  border-radius 15%
  background: radial-gradient(#FFEDBD,#FFD35C);

h1
  text-align center
  vertical-align middle
  line-height 36vw
  font-size 6vw
  font-family 'segment7standard'
  transform scale(1,2)
h2
  text-align left
  padding-left 2rem
  font-size 3rem
  position absolute
ul
  list-style-type none
  padding 0

li
  display inline-block
  margin 0 10px

a
  color #42b983
</style>
